import ListBox from '../../components/listBox';
import ProblemBox from '../../components/problemBox';

function HomePageMain() {

    return (
    <main>

    <ListBox
    title='Objetivos'
    items={[
        'Promover la pasión por las matemáticas a la mayor cantidad de personas posibles.',
        'Facilitar teoría y ejercicios didácticos de formar gratuita.',
        'Mejorar el rendimiento de los estudiantes de nivel secundaria en esta área.',
        'Desarrollar el razonamiento entre los estudiantes, mediante problemas no mecanizados.',
        'Demostrar que cualquiera puede aprender matemáticas, si se esfuerza en hacerlo.'
    ]}
    background={{image: {src: '/TestOfEasyMath/assets/images/board.png', alt: 'board.png'}}}
    />

    <ListBox
    title='Recomendaciones'
    items={[
        'Recuerda, lo importante es entender los conceptos, más que memorizar formulas solo porque sí.',
        'A parte de la información que se te brinda, lo mejor es investigar más, ya sea, mediante libros, internet, entre otras fuentes.',
        'Para un mayor aprendizaje solo observa la solución de los problemas dados, después de haber intentado resolverlos como mínimo 7 u 8 minutos.',
        'Si no comprendes bien algunos conceptos explicados, lo mejor es leerlos nuevamente.',
        'Estudia al menos una hora al día, si quieres ser bueno en esta área tan hermosa.'
    ]}
    background={{image: {src: '/TestOfEasyMath/assets/images/board.png', alt: 'board.png'}}}
    />

    <ProblemBox
    title='Reto de la semana'
    background={{image: {src: '/TestOfEasyMath/assets/images/pi.jpg', alt: 'pi.jpg'}}}
    >

    <p className='problemBox_commonText'>
        Se tiene la siguiente progresión aritmética:
        <br/>
        <br/>
        <span className='problemBox_overline'>ab</span>; <span className='problemBox_overline'>a(b+4)</span>; <span className='problemBox_overline'>b(b-2)</span>; ...; <span className='problemBox_overline'>(b+1)(b+1)b</span> de 156 términos.
        <br/>
        <br/>
        Halle la suma de las cifras de la suma de términos de la siguiente sucesión:
        <br/>
        <br/>
        <span className='problemBox_overline'>(b-1)(a-2)</span>; <span className='problemBox_overline'>a(b+1)</span>; <span className='problemBox_overline'>b(a-4)</span>; <span className='problemBox_overline'>(b+3)(a+2)</span>
    </p>

    </ProblemBox>

    </main>
    );

}

export default HomePageMain;