import { useState, createContext } from 'react';

const w = window;

const ResponsiveContext = createContext();

const ResponsiveProvider = ({ children }) => {

    const getTypeOfScreen = size => {
    
        if (size <= 425) {
    
            return 'mobile';
    
        } else {
    
            return 'computer';
    
        }
    
    }

    const [typeWidthScreen, setTypeWidthScreen] = useState(getTypeOfScreen(w.innerWidth));

    window.addEventListener('resize', e => {

        const currentTypeScreen = getTypeOfScreen(w.innerWidth);

        if (currentTypeScreen === typeWidthScreen) return;

        setTypeWidthScreen(currentTypeScreen);

    })

    const data = { typeWidthScreen };

    return <ResponsiveContext.Provider value={data}>{children}</ResponsiveContext.Provider>;

}

export { ResponsiveProvider };
export default ResponsiveContext;