import { useState } from 'react';
import './style.css'

function ListBox({ title, items, background }) {

    const renderItems = () => {

        return items.map((el, i) => {

            return <li key={i} className='_list_item'>{el}</li>;

        })

    }

    const [backgroundColor, setBackgroundColor] = useState('rgba(0 0 0 /.5)');
    
    const attactBackground = () => {

        if (background['image']) {

            return (
            <figure className='listBox_backgroundListBoxImageContainer'>

                <img src={background['image']['src']} alt={background['image']['alt']} className='_backgroundListBoxImageContainer_image'/>

            </figure>
            );

        } else if (background['color']) {

            setBackgroundColor(background['color']);

        }

    }

    return (
    <div className='listBox' style={{ backgroundColor }}>

        {attactBackground()}

        <div className='listBox_listBoxTitleContainer'>

            <h3 className='_listBoxTitleContainer_title'>{title}</h3>

        </div>

        <ul className='listBox_list'>

            {renderItems()}

        </ul>

    </div>
    );

}

export default ListBox;