import { useState, useContext } from 'react';
import HeaderLogo from './headerLogo';
import MenuButton from './menuButton';
import SearchInput from './searchInput';
import CourseTable from './coursesTable';
import ResponsiveContext from '../../context/ResponsiveContext';
import './style.css';

function Navigator() {

    const [ifCloseNavigator, setIfCloseNavigator] = useState(false),
        { typeWidthScreen } = useContext(ResponsiveContext);

    const decideIfAppearSearch = () => {

        if (typeWidthScreen === 'computer') {

            return <SearchInput/>;

        }

    }

    return (
    <nav className={`navigator${ifCloseNavigator ? ' fixNavigator' : ''}`}>

        <HeaderLogo/>

        <MenuButton
            ifCloseNavigator={ifCloseNavigator}
            setIfCloseNavigator={setIfCloseNavigator}
        />

        <CourseTable
            SearchInput={SearchInput}
            ifCloseNavigator={ifCloseNavigator}
            setIfCloseNavigator={setIfCloseNavigator}
        />

        {decideIfAppearSearch()}

    </nav>
    );

}

export default Navigator;