import { useState, useEffect, useRef } from 'react';
import gsap from 'gsap';
import './style.css';

function Class() {

    const number = useRef(null),
        box = useRef(null);

    const [valueOfNumber, setValueOfNumber] = useState(6);

    useEffect(() => {

        const tl = gsap.timeline({delay: 2, repeat: -1, defaults: {duration: 2}});

        tl.to(number.current, {

            left: '50%',
            opacity: 0,
            onStart: () => setValueOfNumber(6)

        })

        for (let i = 0; i < 5; i++) {

            tl.to(box.current, {

                y: 15,
                rotate: 180,
                borderRadius: '20%',
                duration: .2

            }).to(box.current, {

                y: -15,
                rotate: 180,
                borderRadius: 0,
                duration: .2

            })

        }

        tl.to(box.current, {

            y: 0,
            duration: .1,
            onComplete: () => setValueOfNumber(16)

        })

        tl.to(number.current, {

            left: 'calc(100% - 2rem)',
            opacity: 1

        })

    }, [])

    return (
    <main className='class'>

        <div className='class_classNameContainer'>

            <h2 className='_classNameContainer_className'>Sucesiones</h2>

        </div>

        <figure className='class_mainClassImageContainer'>

            <img src='/TestOfEasyMath/assets/images/fractal.jpg' className='_mainClassImageContainer_mainClassImage'/>

        </figure>

        <div className='class_subTopic'>

            <div className='_subTopic_subTopicTitleContainer'>

                <h3 className='_subTopicTitleContainer_subTopicTitle1'>¿Qué son las sucesiones?</h3>

            </div>

            <p className='class_paragraph'>Una sucesión es un tipo de función en la cual el dominio pertenece a los números naturales, mientras que los elementos del rango de la función (que son conocidos como términos), pueden pertenecer a los números naturales (1, 2, 3), reales (0.6, √3), complejos (0.5i, 6.5, 4), entre otros.</p>
            <p className='class_paragraph'>Para entender mejor lo anterior mencionado, podemos imaginar que las sucesiones son como una máquina, en la cual introduces un número (Dominio) y obtienes otro (Rango), tal como se muestra en la siguiente animación:</p>

            <div className='class_classAnimation'>

                <div ref={el => {number.current = el}} className='_classAnimation_number'>{valueOfNumber}</div>
                <div ref={el => {box.current = el}} className='_classAnimation_machine'></div>

            </div>

        </div>

        <div className='class_subTopic'>

            <div className='_subTopic_subTopicTitleContainer'>

                <h3 className='_subTopicTitleContainer_subTopicTitle1'>¿Cómo se puede representar una sucesión?</h3>

            </div>

            <p className='class_paragraph'>Una sucesión se puede representar principalmente mediante los siguientes 3 métodos:</p>

            <div className='class_subtopic'>

                <div className='_subTopic_subTopicTitleContainer'>

                    <h3 className='_subTopicTitleContainer_subTopicTitle2'>Método analítico:</h3>

                </div>

                <p className='class_paragraph'>Este método es utilizado cuando la sucesión que queremos representar tiene alguna regla de correspondencia, por lo que no necesitaremos el valor de un término para poder hallar cualquier otro.</p>

                <ul className='class_classList'>

                    <li className='_classList_item'>{'{'} n<sup>4</sup> - 1 {'}'} = [0, 15, 80, …];</li>
                    <li className='_classList_item'>{'{'} n + 1 {'}'} = [2, 3, 4, …];</li>
                    <li className='_classList_item'>{'{'} 10n {'}'} = [10, 20, 30, …];</li>

                </ul>

            </div>

            <div className='class_subtopic'>

                <div className='_subTopic_subTopicTitleContainer'>

                    <h3 className='_subTopicTitleContainer_subTopicTitle2'>Método recurrente:</h3>

                </div>

                <p className='class_paragraph'>Con este método necesitaremos declarar al menos el valor de un término, preferiblemente el primero, luego necesitaremos hallar una ecuación que describa como hallar cualquier termino, a partir de los anteriores términos. </p>

                <ul className='class_classList'>

                    <li className='_classList_item'>a<sub>n</sub> = a<sub>n-1</sub> + 3; a<sub>1</sub> = 5;</li>
                    <li className='_classList_item'>t<sub>n+1</sub> = t<sup>2</sup><sub>n</sub>; t<sub>1</sub> = 4;</li>

                </ul>

                <p className='class_paragraph'>Aclaración: Las variables “a” o “t” en este ejemplo hacen referencia a los términos de su respectiva sucesión, mientras que el subíndice “n” hace referencia al dominio de una sucesión.</p>
                <p className='class_paragraph'>Si recuerdas el inicio, se mencionó que el dominio n tiene que ser un número natural, eso es porque el dominio representa el orden de los términos, por ejemplo, tu no podrías decir: “Hola Fernanda, ¿Sabes el nombre de la persona que se encuentra en el orden √5 de la fila?”, por el hecho de que no tiene sentido.</p>

            </div>

            <div className='class_subtopic'>

                <div className='_subTopic_subTopicTitleContainer'>

                    <h3 className='_subTopicTitleContainer_subTopicTitle2'>Método descriptivo:</h3>

                </div>

                <p className='class_paragraph'>La característica de este método es que muestra los primeros términos de una sucesión, de tal forma que se puedan hallar los siguientes. Normalmente es beneficio representar nuestras sucesiones de esta forma, cuando los términos son números especiales, fechas de nacimientos de un grupo de persona, etc.</p>

                <ul className='class_classList'>

                    <li className='_classList_item'>[2, 3, 5, 7, 11, …] (Números primos);</li>
                    <li className='_classList_item'>[3, 19, 8, 31, 6, 1, …] (Días de cumpleaños de un grupo de personas);</li>

                </ul>

            </div>

        </div>

        <div className='class_subTopic'>

            <div className='_subTopic_subTopicTitleContainer'>

                <h3 className='_subTopicTitleContainer_subTopicTitle1'>Problemas de ejemplo</h3>

            </div>

            <div className='class_subTopic'>

                <div className='_subTopic_subTopicTitleContainer'>

                    <h3 className='_subTopicTitleContainer_subTopicTitle2'>1.- Representa mediante el método recurrente la siguiente sucesión:</h3>
                    
                </div>

                <p className='class_paragraph'>S = [3, 7, 11, 15, 19, ...];</p>

                <div className='class_subTopic'>

                    <div className='_subTopic_subTopicTitleContainer'>

                        <h3 className='_subTopicTitleContainer_subTopicTitle2'>Solución:</h3>

                    </div>

                    <p className='class_paragraph'>
                        Para resolver este tipo de problemas, entonces debemos seguir los siguientes pasos:
                    </p>

                    <ul className='class_classList'>

                        <li className='class_paragraph'>Encuentra un patrón con el cual puedas hallar cada término, en este caso el patrón consiste en que cada término es 4 más que el anterior (7 - 3 = 4; 11 - 7 = 4;).</li>
                        <li className='class_paragraph'>Luego halla el primer término de la sucesión, que en este caso es 3.</li>

                    </ul>

                    <p className='class_paragraph'>
                        Con los anteriores datos recolectados, entonces la respuesta es:
                    </p>

                    <p className='class_paragraph'>S<sub>n</sub> = S<sub>n - 1</sub> + 4; S<sub>1</sub> = 3;</p>

                </div>

            </div>

            <div className='class_subTopic'>

                <div className='_subTopic_subTopicTitleContainer'>

                    <h3 className='_subTopicTitleContainer_subTopicTitle2'>2.- Representa mediante el método análitico la siguiente sucesión:</h3>
                    
                </div>

                <p className='class_paragraph'>S = [7, 9, 11, 13, 15, ...];</p>

                <div className='class_subTopic'>

                    <div className='_subTopic_subTopicTitleContainer'>

                        <h3 className='_subTopicTitleContainer_subTopicTitle2'>Solución:</h3>

                    </div>

                    <p className='class_paragraph'>
                        Dado que el patrón de esta sucesión consiste en sumar 2 al término S<sub>n</sub> para hallar el término S<sub>n + 1</sub>, y el primer término es 7. Entonces formulamos la siguiente ecuación:
                    </p>

                    <p className='class_paragraph'>S<sub>n</sub> = 7 + 2(n-1) = 2n + 5</p>

                    <p className='class_paragraph'>Entonces la solución de este problema es:</p>

                    <p className='class_paragraph'>{'{ 2n + 5 };'}</p>

                </div>

            </div>

        </div>

    </main>
    );

}

export default Class;