import { useState, useEffect, useContext } from 'react';
import useFetchMedia from '../../../hooks/useFetchMedia';
import ResponsiveContext from '../../../context/ResponsiveContext';
import './style.css';

function MenuButton({ ifCloseNavigator, setIfCloseNavigator }) {

    const { typeWidthScreen } = useContext(ResponsiveContext),
        [menuIcons, setMenuIcons] = useState({});

    const menuIcon = useFetchMedia('/TestOfEasyMath/assets/images/icons/menu.svg')['media'],
        exitIcon = useFetchMedia('/TestOfEasyMath/assets/images/icons/exit.svg')['media'];

    useEffect(() => {

        if (!(menuIcon && exitIcon)) return;

        setMenuIcons({

            menu: URL.createObjectURL(menuIcon),
            exit: URL.createObjectURL(exitIcon)

        });

    }, [menuIcon, exitIcon])

    const toggleCoursesBar = () => setIfCloseNavigator(ifCloseNavigator ? false : true),
        decideWhatTypeOfIconAppear = () => {

            if (typeWidthScreen === 'computer') {
    
                return 'Cursos';
    
            } else {
    
                return <img
                    className='menuButton_image'
                    src={menuIcons[ifCloseNavigator ? 'exit':'menu']}
                    alt={`${ifCloseNavigator ? 'exit' : 'menu'}.svg`}
                />;
    
            }

        };

    return (
        <button className='menuButton' onClick={toggleCoursesBar}>
            {decideWhatTypeOfIconAppear()}
        </button>
    );

}

export default MenuButton;