import { useContext } from 'react';
import ResponsiveContext from '../../../context/ResponsiveContext';
import Course from './course';
import './style.css';

function CoursesTable({ SearchInput, ifCloseNavigator, setIfCloseNavigator }) {

    const { typeWidthScreen } = useContext(ResponsiveContext),
        coursesContent = [
            {title: 'Aritmética', sessionsContent: [
                {title: 'Sucesiones', url: '/class/arithmetic/successions'},
                {title: 'Divisibilidad', url: '/class/arithmetic/divisibility'},
                {title: 'Máximo común divisor y Mínimo común múltiplo', url: '/class/arithmetic/greatest-common-divisor-and-least-common-multiple'}
            ]},
            {title: 'Geometría', sessionsContent: []},
            {title: 'Algebra', sessionsContent: []},
            {title: 'Trigonometría', sessionsContent: []},
            {title: 'Raz. Matemático', sessionsContent: []}
        ];

    const decideIfAppearSearch = () => {

        if (typeWidthScreen === 'mobile') {

            return <SearchInput/>;

        }

    }

    const renderCourses = () => {

        return coursesContent.map(el => {

            return <Course
            key={el.title}
            title={el.title}
            sessionsContent={el.sessionsContent}
            ifCloseNavigator={ifCloseNavigator}
            setIfCloseNavigator={setIfCloseNavigator}
            />;

        })

    }

    return (
    <div 
        className={`coursesTableContainer${ifCloseNavigator ?
            ' appearCoursesTableContainer' : ''}`
    }>

        {decideIfAppearSearch()}

        <div className='coursesTableContainer_coursesTable'>

            {renderCourses()}

        </div>

    </div>
    );

}

export default CoursesTable;