import { Link } from 'react-router-dom';
import './style.css';

function Session({ title, url, ifCloseNavigator, setIfCloseNavigator }) {

    return (
    <Link onClick={() => setIfCloseNavigator(ifCloseNavigator ? false : true)} className='session' to={url}>

        <h3 className='session_title'>{title}</h3>

    </Link>
    );

}

export default Session;