import { useState, useEffect } from 'react';
import Session from './session';
import './style.css';

function Course({ title, sessionsContent, ifCloseNavigator, setIfCloseNavigator }) {

    const renderSessions = () => {

        return sessionsContent.map(el => {

            return <Session
            key={el.title}
            ifCloseNavigator={ifCloseNavigator}
            setIfCloseNavigator={setIfCloseNavigator}
            title={el.title}
            url={el.url}
            />;

        });

    }

    const [hasBeenCourseClicked, setHasBeenCourseClicked] = useState(false),
        [chevronIcons, setChevronIcons] = useState({});

    // the next useEffect get icons

    useEffect(() => {

        async function getIcons() {

            try {

                const chevronDownIcon = await fetch('/TestOfEasyMath/assets/images/icons/chevron-down.svg'),
                    chevronUpIcon = await fetch('/TestOfEasyMath/assets/images/icons/chevron-up.svg');
                
                setChevronIcons({

                    chevronDown: URL.createObjectURL(await chevronDownIcon.blob()),
                    chevronUp: URL.createObjectURL(await chevronUpIcon.blob())

                });

            } catch(err) {
    
                console.error(err);
    
            }

        }

        getIcons();

    }, [])

    const toggleSessions = () => setHasBeenCourseClicked(hasBeenCourseClicked ? false : true);

    return (
    <div className='courseContainer'>

        <div className='courseContainer_course' onClick={toggleSessions}>

            <h3 className='_course_title'>{title}</h3>
            <img
                className='_course_chevron'
                src={chevronIcons[hasBeenCourseClicked ? 'chevronUp' : 'chevronDown']}
                alt={`${hasBeenCourseClicked ? 'chevronUp' : 'chevronDown'}.svg`}
            />

        </div>

        <div className={`courseContainer_${hasBeenCourseClicked ? 'open' : 'closed'}SessionsContainer`}>

            {renderSessions()}

        </div>

    </div>
    );

}

export default Course;