import { useState, useEffect } from "react";

function useFetchMedia(url) {

    const [media, setMedia] = useState(null),
        [isPending, setIsPending] = useState(true),
        [error, setError] = useState(null);

    useEffect(() => {

        const getMedia = async url => {

            try {

                const res = await fetch(url);

                if (!res.ok) {

                    throw { err: true, status: res.status, statusText: res.statusText || 'An error occurred' }

                }

                setMedia(await res.blob());
                setIsPending(false);
                setError({ err: false });

            } catch(err) {

                setError(err)

            }

        }

        getMedia(url);

    }, [url])

    return { media, isPending, error };

}

export default useFetchMedia;