import { Link } from 'react-router-dom';
import './style.css';

function HeaderLogo() {

    return (
    <Link to='/'>

        <figure className='headerLogo'>

            <img className='headerLogo_image' src='/TestOfEasyMath/assets/images/easyMathLogo.png' alt='Easy Math Logo'/>

            <figcaption className='headerLogo_title'>EasyMath</figcaption>

        </figure>

    </Link>
    );

}

export default HeaderLogo;