import './style.css';

function SearchInput() {

    return (
    <div className='searchContainer'>
        <figure className='searchContainer_imageSearchContainer'>

            <img className='_imageSearchContainer_imageSearch' alt='search.svg' src='/TestOfEasyMath/assets/images/icons/search.svg'/>

        </figure>
        <input className='searchContainer_searchInput' placeholder='Buscar'/>
    </div>
    );

}

export default SearchInput;