import { useState } from 'react';
import './style.css';

function ProblemBox({ title, children, background }) {

    const backgroundColor = background['color'] || 'rgba(0 0 0 /.5)';
    
    const attactBackground = () => {

        if (background['image']) {

            return (
            <figure className='problemBox_backgroundProblemBoxImageContainer'>

                <img src={background['image']['src']} alt={background['image']['alt']} className='_backgroundProblemBoxImageContainer_image'/>

            </figure>
            );

        }

    }

    return (
    <div className='problemBox' style={{backgroundColor}}>

        {attactBackground()}

        <div className='problemBox_problemTitleContainer'>

            <h3 className='_problemBoxTitleContainer_title'>{title}</h3>

        </div>

        {children}

    </div>
    );

}

export default ProblemBox;