import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import useFetchMedia from '../../../hooks/useFetchMedia';
import './style.css'

function IconBox({ to, imageLink, alt }) {

    const [imageURL, setImageURL] = useState(null),
        icon = useFetchMedia(imageLink)['media'];

    useEffect(() => {

        if (!icon) return;
    
        setImageURL(URL.createObjectURL(icon));
    
    }, [icon])

    return (
    <Link className='iconBox' to={to}>
        
        <img className='iconBox_image' src={imageURL} alt={alt}/>

    </Link>
    );

}

export default IconBox;