import IconBox from "./iconBox";
import './style.css';

function Footer() {

    return (
    <footer className='footer'>

        <div className='footer_footerTitleContainer'>

            <h3 className='_footerTitleContainer_title'>Siguenos:</h3>

        </div>

        <div className='footer_iconsContainer'>

            <IconBox to='/' imageLink='/TestOfEasyMath/assets/images/icons/facebook.svg' alt='facebook.svg'/>
            <IconBox to='/' imageLink='/TestOfEasyMath/assets/images/icons/twitter.svg' alt='twitter.svg'/>
            <IconBox to='/' imageLink='/TestOfEasyMath/assets/images/icons/instagram.svg' alt='instagram.svg'/>

        </div>

    </footer>
    );

}

export default Footer;